import ReactDOM from 'react-dom/client';
import './index.css';
import App from './App';
import Upowszechnianie from './Upowszechnianie';
import Wycieczki from './Wycieczki';
import Rezultaty from './Rezultaty';
import { HashRouter as Router, Routes, Route } from 'react-router-dom';

const root = ReactDOM.createRoot(
  document.getElementById('root') as HTMLElement
);

root.render(
  <Router>
    <Routes>
      <Route path="/" element={<App />} />
      <Route path="/upowszechnianie" element={<Upowszechnianie />} />
      <Route path="/wycieczki" element={<Wycieczki />} />
      <Route path="/rezultaty" element={<Rezultaty />} />
      <Route path="*" element={<App />} />
    </Routes>
  </Router>
);