import './App.css';

import React, { useEffect } from 'react';

import Navbar from './components/Navbar';

import useMobile from './hooks/useMobile';

import SanMarino1 from './img/sanmarino/san_marino_1.jpg';
import SanMarino2 from './img/sanmarino/san_marino_2.jpg';
import SanMarino3 from './img/sanmarino/san_marino_3.jpg';
import SanMarino4 from './img/sanmarino/san_marino_4.jpg';

function Gallery(props: { images: { src: string, alt: string }[] }) {
    return (
        <div className='Gallery'>
            {props.images.map((image) => {
                return (
                    <div className='Gallery__image'>
                        <a href={image.src} target='_blank'><img src={image.src} alt={image.alt} width={'100%'} /></a>
                    </div>
                )
            })}
        </div>
    )
}

function Wycieczka(props: { title: string, text: string, images: { src: string, alt: string }[] }) {
    return (
        <div className='Wycieczka'>
            <div className='Wycieczka__title'>
                {props.title}
            </div>
            <Gallery images={props.images} />
            <div className='Wycieczka__text'>
                {props.text}
            </div>
            <hr></hr>
        </div>
    )
}

export default function Wycieczki() {
    useEffect(() => {
        window.scrollTo({top: 0});
    }, [])
    const sanmarino_gallery = [
        {
            src: SanMarino4,
            alt: 'San Marino 4'
        },
        {
            src: SanMarino3,
            alt: 'San Marino 3'
        },
        {
            src: SanMarino1,
            alt: 'San Marino 1'
        },
        {
            src: SanMarino2,
            alt: 'San Marino 2'
        },
    ]

    return (
        <>
        <Navbar />
        <div className='App__container'>
            <div className='App'>
                <div className='App__content'>
                    <div className='App__content__title'>
                        Wycieczki i czas wolny
                    </div>
                    <div className='App__content__text'>
                        Uczniowie mieli możliwość spędzenia czasu wolny na wiele ciekawych sposobów.<br></br>Jednym z nich były wycieczki do przeróżnych miejsc.
                    </div>
                    <Wycieczka title='Wycieczka do San Marino' text='W ramach poznawania kultury wybraliśmy się do San Marino. Wcześniej kilkoro z nas przygotowało plan wycieczki i przewodnik po tym państwie. Dzień wcześniej Hubert i Piotr przedstawili nam podstawowe informacje na temat San Marino, jego historii i czasów współczesnych. Jeszcze przed wyjazdem zwróciliśmy uwagę na umiłowanie wolności mieszkańców tego państwa. W trakcie zwiedzając starego miasta, szukaliśmy miejsc, o których mówili koledzy i odkrywaliśmy nowe. Zachwyciła nas topografia tego miasta zbudowanego na skale, podziwialiśmy  budowle, pomniki, a niektórzy dotarli nawet do twierdzy La Rocca o Guaita i zwiedzili ten obiekt powstały w XI wieku. Kupując bilet do twierdzy,  nabyliśmy też możliwość wejścia do siedziby władz San Marino, Palazzo Pubblico.  Zachwyca szczególnie  sala parlamentu, w której ściany zdobią piękne malowidła ścienne. Sala jest użytkowana, na stolikach stoją laptopy, jest kilka telewizorów, zamontowano mikrofony. Odbywają się tak obrady władz, a kiedy władze nie obradują, turyści mogą salę zwiedzać.  San Marino umiejętnie łączy piękną historię ze współczesnością. W mieście wiele jest współczesnych rzeźb i pomników upamiętniających ważne i tragiczne wydarzenia, jak te z czasów II wojny światowej czy tragedię dzieci z Biesłana. Jedną ze współczesnych atrakcji w dniu naszej wycieczki była impreza Steam party 2023 oraz wystawa Lego.' images={sanmarino_gallery} />
                </div>
            </div>
            <div className='App__background'></div>
        </div>
        </>
    )
}