function Gallery(props: { images: { src: string, alt: string }[] }) {
    return (
        <div className='Gallery'>
            {props.images.map((image) => {
                return (
                    <div className='Gallery__image'>
                        <a href={image.src} target='_blank'><img src={image.src} alt={image.alt} width={'100%'} /></a>
                    </div>
                )
            })}
        </div>
    )
}

const contentHeight = '640px';
function Pdf(props: { src: string }) {
    return (
        <object data={props.src} type="application/pdf" width="100%" height={contentHeight}></object>
    )
}

function YouTubeVideo(props: { src: string }) {
    return (
        <div className='YouTubeVideo'>
            <iframe style={{border: 'none', aspectRatio: '16/9'}} width="100%" height='auto' src={props.src} allowFullScreen></iframe>
        </div>
    )
}

